import {UserContext} from "../contexts/user-context";

function UserInfo() {
    return (
        <div>
            <UserContext.Consumer>
                {
                    stateData => {
                        const {id, username, age} = stateData.state;
                        return (
                            <div>
                                <text>{id}</text>
                                <text>{username}</text>
                                <text>{age}</text>
                            </div>
                        )
                    }
                }
            </UserContext.Consumer>
        </div>
    )
}

export default UserInfo;